.features
{
	&__text
	{
		max-width: 570px;
		margin-bottom: 100px;

		@media screen and (max-width: 576px)
		{
			margin-bottom: 20px;
		}
	}

	&__list
	{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-top: 0;
		margin-bottom: 0;
		margin-bottom: -130px;
		padding-left: 0;

		list-style: none;

		@media screen and (max-width: 576px)
		{
			margin-bottom: -40px;
		}
	}

	&__item
	{
		padding-bottom: 130px;

		@media screen and (max-width: 576px)
		{
			padding-bottom: 40px;
		}
	}
}

.features-item
{
	width: 100%;
	max-width: 290px;

	@media screen and (max-width: 576px)
	{
		display: flex;
		align-items: center;
		flex-direction: column;

		margin-right: auto;
		margin-left: auto;

		.text
		{
			text-align: center;
		}
	}

	@media screen and (max-width: 768px)
	{
		max-width: 250px;
	}

	@media screen and (max-width: 576px)
	{
		max-width: 330px;
	}

	&__wrapper
	{
		position: relative;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 180px;
		height: 180px;
		margin-bottom: 40px;

		border: solid 1px #f76b00;
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 33px 49px 0 rgba(0, 0, 0, .05);

		@media screen and (max-width: 576px)
		{
			margin-bottom: 20px;
		}

		&::before
		{
			position: absolute;
			top: 50%;
			left: 50%;

			width: 117px;
			height: 117px;

			content: "";
			transform: translate(-50%, -50%);

			border: 16px solid #fef0e5;
			border-radius: 50%;
			background-color: #fde3ce;
		}

		&::after
		{
			position: absolute;
			top: 0;
			left: 50%;

			width: 60px;
			height: 8px;

			content: "";
			transform: translateX(-50%);

			border-radius: 4px;
			background-color: $color-orange;
		}
	}

	&__icon
	{
		position: relative;
		z-index: 10;
	}
}
