.button
{
	font-weight: 700;

	display: inline-flex;

	padding-top: 10px;
	padding-right: 55px;
	padding-bottom: 10px;
	padding-left: 55px;

	transition-duration: .2s;
	text-align: center;
	text-decoration: none;

	color: $color-white;
	border: none;
	border-radius: 5px;
	background-color: $color-orange;
	box-shadow: 0 0 21.87px 5.13px rgba(247, 107, 0, .34);

	@media screen and (max-width: 576px)
	{
		justify-content: center;

		width: 100%;
	}

	&:hover
	{
		background-color: darken($color-orange, 20%);
	}

	&_blue
	{
		background-color: $color-blue;
		box-shadow: 0 0 21.87px 5.13px rgba(3, 38, 166, .34);

		&:hover
		{
			background-color: darken($color-blue, 20%);
		}
	}

	&_white
	{
		color: $color-orange;
		background-color: $color-white;
		box-shadow: none;

		&:hover
		{
			color: $color-white;
		}
	}
}
