.address
{
	position: relative;

	padding-left: 40px;

	&::before
	{
		position: absolute;
		top: 0;
		left: 0;

		content: "";

@include sprite("location");
	}

	&__location
	{
		font-size: 22px;
		font-weight: 700;

		margin-top: 0;
		margin-bottom: 15px;

		color: $color-orange;
	}

	&__schedule
	{
		margin-top: 0;
		margin-bottom: 0;

		span
		{
			font-weight: 700;
		}
	}
}
