.questions
{
	background: url("../images/questions.png") no-repeat 100% 0, linear-gradient(144deg, #363636, #1b1c1c);
	background-size: contain;
	box-shadow: inset 0 30px 46px 0 rgba(0, 0, 0, .27);

	&__inner
	{
		background-image: url("../images/cone.png");
		background-repeat: no-repeat;
		background-position: 100% 50%;

		@media screen and (max-width: 1200px)
		{
			background-size: 17%;
		}

		@media screen and (max-width: 992px)
		{
			background-size: 25%;
		}

		@media screen and (max-width: 576px)
		{
			background-image: none;
		}
	}

	&__title,
	&__text
	{
		color: $color-white;
	}

	&__text
	{
		max-width: 390px;
	}

	&__form
	{
		display: flex;
		align-items: flex-end;
		justify-content: space-between;

		max-width: 800px;
		padding-top: 100px;

		@media screen and (max-width: 992px)
		{
			align-items: flex-start;
			flex-direction: column;

			padding-top: 50px;
		}
	}

	&__input
	{
		@media screen and (max-width: 992px)
		{
			margin-bottom: 20px;
		}

		@media screen and (max-width: 576px)
		{
			max-width: 100%;
		}
	}
}
