.popup-form
{
	display: flex;
	align-items: center;
	flex-direction: column;

	width: 100%;
	max-width: 475px;
	margin-right: auto;
	margin-left: auto;
	padding: 40px;

	border-radius: 5px;
	background-color: $color-orange;

	&__title
	{
		font-size: 30px;
		font-weight: 400;

		margin-top: 0;
		margin-bottom: 20px;

		text-align: center;
		text-transform: uppercase;

		color: $color-white;
	}

	&__text
	{
		font-size: 18px;

		margin-top: 0;

		text-align: center;

		color: $color-white;
	}

	&__input
	{
		margin-bottom: 10px;

		border-color: $color-white;

		&:last-of-type
		{
			margin-bottom: 40px;
		}
	}
}
