.container
{
	min-width: 330px;
	max-width: 1170px;
	margin-right: auto;
	margin-left: auto;
	padding-right: 15px;
	padding-left: 15px;

	@media screen and (max-width: 1200px)
	{
		max-width: 960px;
	}

	@media screen and (max-width: 992px)
	{
		max-width: 720px;
	}

	@media screen and (max-width: 768px)
	{
		max-width: 540px;
	}

	@media screen and (max-width: 576px)
	{
		max-width: 100%;
	}
}
