.input
{
	font-size: 16px;
	font-weight: 300;

	width: 100%;
	max-width: 235px;
	padding-top: 15px;
	padding-bottom: 15px;

	text-align: center;

	color: $color-white;
	border: none;
	border-bottom: 1px solid $color-orange;
	background-color: transparent !important;

	&:required:valid
	{
		border-color: green;
	}

	&:focus
	{
		outline: none;
	}

	&::placeholder
	{
		font-weight: 300;

		color: $color-white;
	}

	&_animated
	{
		animation: shake .82s cubic-bezier(.36,.07,.19,.97) both;
	}
}

@keyframes shake
{
	10%,
	90%
	{
		transform: translate3d(-1px, 0, 0);
	}

	20%,
	80%
	{
		transform: translate3d(2px, 0, 0);
	}

	30%,
	50%,
	70%
	{
		transform: translate3d(-4px, 0, 0);
	}

	40%,
	60%
	{
		transform: translate3d(4px, 0, 0);
	}
}
