.preview
{
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	&__inner
	{
		display: flex;
		align-content: flex-end;
		flex-grow: 1;
		flex-wrap: wrap;

		padding-bottom: 400px;
	}

	&__title
	{
		max-width: 800px;
	}

	&__text
	{
		width: 100%;
		margin-bottom: 3.5em;

		b
		{
			font-weight: 400;

			color: $color-orange;

@include fluidFontSize(34px, 45px, 576px, 1200px, 34px);
		}
	}

	&__button
	{
		margin-right: 20px;

		@media screen and (max-width: 768px)
		{
			margin-right: 0;
			margin-bottom: 20px;
		}
	}
}
