.thanks
{
	display: flex;
	align-items: center;
	flex-direction: column;

	width: 100%;
	max-width: 475px;
	margin-right: auto;
	margin-left: auto;
	padding: 40px;

	border-radius: 5px;
	background-color: $color-orange;

	&__icon
	{
		margin-bottom: 35px;
	}

	&__title,
	&__text
	{
		max-width: 225px;
	}

	&__title
	{
		font-size: 30px;
		font-weight: 400;

		margin-top: 0;
		margin-bottom: 20px;

		text-align: center;
		text-transform: uppercase;

		color: $color-white;
	}

	&__text
	{
		font-size: 18px;
		font-weight: 300;

		margin-top: 0;

		text-align: center;

		color: $color-white;
	}
}
