.advantages
{
	&__title
	{
		max-width: 600px;
	}

	&__text
	{
		max-width: 410px;
		margin-bottom: 5.5em;

		@media screen and (max-width: 768px)
		{
			margin-bottom: 3em;
		}
	}

	&__list
	{
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		margin-top: 0;
		margin-right: -15px;
		margin-bottom: 0;
		margin-bottom: -40px;
		margin-left: -15px;
		padding-left: 0;

		list-style: none;

		counter-reset: section;
	}

	&__item
	{
		padding-right: 15px;
		padding-bottom: 40px;
		padding-left: 15px;
	}
}

.advantages-item
{
	&__inner
	{
		font-size: 18px;

		position: relative;

		display: flex;
		align-items: center;

		width: 100%;
		max-width: 360px;
		min-height: 285px;
		padding: 40px;

		color: $color-white;
		border-radius: 5px;
		background: url("../images/advantages-item.png") no-repeat 100% 100%, linear-gradient(133deg, #f76b00, #cc5800);

		@media screen and (max-width: 576px)
		{
			max-width: 100%;
		}

		&::before
		{
			font-size: 45px;

			position: absolute;
			top: -20px;
			left: 40px;

			display: flex;
			align-items: center;
			justify-content: center;

			width: 90px;
			height: 90px;

			content: counter(section) "";
			counter-increment: section;

			color: $color-blue;
			border: solid 1px $color-orange;
			border-radius: 10px;
			background-color: $color-white;
			box-shadow: 0 33px 49px 0 rgba(0, 0, 0, .25);
		}
	}

	&:nth-child(-n+9) &__inner::before
	{
		content: "0"counter(section) "";
	}
}
