.contacts
{
	position: relative;

	padding-left: 40px;

	&::before
	{
		position: absolute;
		top: 0;
		left: 0;

		content: "";

@include sprite("telephone");
	}

	&__phone-number,
	&__email
	{
		display: block;

		text-decoration: none;
	}

	&__phone-number
	{
		font-size: 22px;
		font-weight: 700;

		margin-bottom: 15px;

		color: $color-orange;
	}

	&__email
	{
		color: $color-gray;
	}
}
