.portfolio
{
	background-image: linear-gradient(331deg, #ddd, $color-white);

	&__list
	{
		margin-top: 0;
		margin-bottom: 0;
		margin-bottom: -100px;
		padding-top: 100px;
		padding-left: 0;

		list-style: none;

		@media screen and (max-width: 992px)
		{
			padding-top: 50px;
		}

		@media screen and (max-width: 576px)
		{
			margin-bottom: -50px;
		}
	}

	&__item
	{
		padding-bottom: 100px;

		@media screen and (max-width: 576px)
		{
			padding-bottom: 50px;
		}
	}
}

.portfolio-item
{
	display: flex;
	align-items: flex-start;
	justify-content: space-between;

	@media screen and (max-width: 992px)
	{
		flex-direction: column;
	}

	&:nth-child(odd) &__info
	{
		padding-left: 70px;

		@media screen and (max-width: 992px)
		{
			padding-left: 0;
		}
	}

	&:nth-child(even) &__info
	{
		order: -1;

		padding-right: 70px;

		@media screen and (max-width: 992px)
		{
			order: 0;

			padding-right: 0;
		}
	}


	& > *
	{
		width: 100%;
		max-width: 560px;

		@media screen and (max-width: 992px)
		{
			max-width: 100%;
		}
	}

	&__wrapper
	{
		position: relative;

		overflow: hidden;

		border-radius: 5px;
		box-shadow: 0 33px 49px 0 rgba($color-black, .25);

		@media screen and (max-width: 992px)
		{
			margin-bottom: 50px;
		}
	}

	&__arrow
	{
		position: absolute;
		top: 50%;

		transition-duration: .2s;

		&:first-of-type
		{
			transform: translate(-100%, -50%);
		}

		&:last-of-type
		{
			right: 0;

			transform: translate(100%, -50%);
		}
	}

	&__wrapper:hover &__arrow:first-of-type,
	&__wrapper:hover &__arrow:last-of-type
	{
		transform: translate(0, -50%);
	}

	&__image
	{
		display: block !important;
	}

	&__desc
	{
		font-size: 18px;
		font-weight: 300;

		margin-top: 0;
		margin-bottom: 1em;

		color: #707070;

		span
		{
			font-weight: 700;
		}

		&:last-of-type
		{
			margin-bottom: 2.8em;
		}
	}
}
