.footer
{
	&__inner
	{
		display: flex;
		align-items: center;

		padding-top: 30px;
		padding-bottom: 30px;

		@media screen and (max-width: 992px)
		{
			flex-direction: column;
		}

		@media screen and (max-width: 576px)
		{
			align-items: flex-start;
		}
	}

	&__logo
	{
		@media screen and (max-width: 992px)
		{
			margin-bottom: 20px;
		}
	}

	&__address
	{
		flex-shrink: 0;

		margin-left: auto;

		@media screen and (max-width: 992px)
		{
			margin-bottom: 20px;
			margin-left: 0;
		}
	}

	&__contacts
	{
		flex-shrink: 0;

		margin-left: 50px;

		@media screen and (max-width: 1200px)
		{
			margin-left: auto;
		}

		@media screen and (max-width: 992px)
		{
			margin-left: 0;
		}
	}
}
