.certificates
{
	background-image: linear-gradient(331deg, #ddd, $color-white);

	&__list
	{
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;

		list-style: none;

		@media screen and (min-width: 992px)
		{
			display: flex;
		}
	}

	&__text
	{
		max-width: 470px;
	}

	.slick-slide > div
	{
		max-width: 450px;
		margin-right: auto;
		margin-left: auto;
	}
}

.certificates-item
{
	position: relative;

	width: 100%;

	background-image: url("../images/frame.png");
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: contain;

	&::before
	{
		display: block;

		padding-bottom: 125.8227%;

		content: "";
	}

	&__inner
	{
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;
		padding-top: 15%;
		padding-right: 14.5%;
		padding-bottom: 14.6%;
		padding-left: 14.2%;

		background-color: $color-orange;
		background-clip: content-box;
	}

	&__link
	{
		display: block;

		padding: 7.65%;
	}

	&__image
	{
		display: block;

		width: 100%;
		height: auto;
	}
}
