.header
{
	&__inner
	{
		display: flex;
		align-items: flex-end;
		justify-content: space-between;

		padding-top: 20px;
		padding-bottom: 20px;

		@media screen and (max-width: 576px)
		{
			align-items: center;
			flex-direction: column;
		}
	}

	&__logo
	{
		@media screen and (max-width: 576px)
		{
			margin-bottom: 20px;
		}
	}

	&__callback
	{
		transform: translateY(25px);

		@media screen and (max-width: 992px)
		{
			transform: translateY(0);
		}
	}

	&__menu
	{
		@media screen and (max-width: 992px)
		{
			display: none;
		}
	}
}
