.renovation
{
	background: url("../images/renovation.png") no-repeat 95% 50%, linear-gradient(331deg, #ddd, #fff);

	&__title
	{
		max-width: 700px;
	}

	&__wrapper
	{
		display: flex;

		@media screen and (max-width: 1200px)
		{
			flex-direction: column;
		}
	}

	&__info
	{
		padding-right: 8.547%;

		@media screen and (max-width: 1200px)
		{
			margin-bottom: 20px;
			padding-right: 0;
		}
	}

	&__video
	{
		position: relative;

		overflow: hidden;
		flex-shrink: 0;

		width: 100%;
		max-width: 585px;

		border-radius: 5px;
		box-shadow: 0 33px 49px 0 rgba(0, 0, 0, .25);

		&::before
		{
			display: block;

			width: 100%;
			padding-bottom: 56.25%;

			content: "";
		}

		iframe
		{
			position: absolute;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;
		}
	}



	&__text
	{
		margin-bottom: 1.4em;

		color: #5d5d5d;

		&:last-of-type
		{
			margin-bottom: 4.2735em;

			@media screen and (max-width: 768px)
			{
				margin-bottom: 2em;
			}
		}
	}
}
