.callback
{
	display: inline-flex;
	align-items: center;
	flex-direction: column;

	@media screen and (max-width: 576px)
	{
		width: 100%;
	}

	&__link
	{
		font-weight: 700;

		position: relative;

		margin-bottom: 25px;
		padding-left: 47px;

		transition-duration: .2s;
		text-decoration: none;

		color: $color-gray;

		&::before
		{
			position: absolute;
			top: 50%;
			left: 0;

			content: "";
			transform: translateY(-50%);

@include sprite("telephone");
		}

		&:hover
		{
			color: lighten($color-gray, 40%);
		}
	}
}
