.menu
{
	display: inline-flex;

	&__list
	{
		display: flex;

		margin-top: 0;
		margin-right: -43px;
		margin-bottom: 0;
		margin-left: -43px;
		padding-left: 0;

		list-style: none;

		@media screen and (max-width: 1200px)
		{
			margin-right: -10px;
			margin-left: -10px;
		}
	}

	&__item
	{
		padding-right: 43px;
		padding-left: 43px;

		@media screen and (max-width: 1200px)
		{
			padding-right: 10px;
			padding-left: 10px;
		}
	}

	&__link
	{
		position: relative;

		text-decoration: none;

		color: $color-gray;

		&::before
		{
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;
			height: 1px;

			content: "";
			transition-duration: .2s;

			opacity: 0;
			background-color: $color-blue;
		}

		&:hover::before
		{
			transform: translateY(10px);

			opacity: 1;
		}
	}
}
